@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
*,*::before,*::after{box-sizing:border-box}body{margin:0;font-family:'Lato', sans-serif;padding:0}ul{list-style:none;padding:0}a{text-decoration:none;color:#000}.container{width:90%;max-width:30rem;height:100vh;min-height:50rem;margin:0 auto;background-color:#f7f7f7;display:flex;flex-direction:column;align-items:center;justify-content:center}.title{font-size:1.5rem;margin-bottom:2rem}.sub-title{font-size:1rem;margin-bottom:0.3rem}.balance{font-size:2rem;margin:0}.summary{display:flex;margin:1.5rem 0 2rem;padding:0.5rem 0;box-shadow:0 0 5px 1px rgba(0,0,0,0.3);text-align:center;background-color:#fff}.summary .devider{border-right:1px solid rgba(0,0,0,0.3)}.summary h3{padding:0 2.5rem}.summary .income-balance{color:#2ecc71}.summary .expense-balance{color:#c0392b}.history{margin-bottom:2rem}.history h3{padding-bottom:0.5rem;border-bottom:solid 1px rgba(0,0,0,0.3)}.history .list{display:flex;justify-content:space-between;width:100%;margin-top:0.3rem;padding:0.4rem 0.6rem;background-color:#fff}.history .true{border-right:3px solid #2ecc71}.history .false{border-right:3px solid #c0392b}.input-form h3{padding-bottom:0.5rem;border-bottom:solid 1px rgba(0,0,0,0.3)}.input-form label{display:block;margin-bottom:1rem}.input-form label input{width:100%;margin-top:0.3rem;padding:0.4rem;font-size:1rem}.input-form .btn{width:100%;padding:0.5rem;font-size:1rem;color:white;background-color:#9c88ff;border:none;outline:none;cursor:pointer}

